'use strict';

// MODULES //

import { isPrimitive as isString } from '@stdlib/assert-is-string';
import hasOwnProp from '@stdlib/assert-has-own-property';
import database from '@stdlib/error-tools-database';
import format from '@stdlib/error-tools-fmtprodmsg';


// VARIABLES //

var db;


// MAIN //

/**
* Returns an error message corresponding to a provided two-character error identifier.
*
* @param {string} id - error identifier
* @throws {TypeError} must provide a string
* @returns {(string|null)} error message
*
* @example
* var v = id2msg( '9b' );
* // returns <string>
*
* @example
* var v = id2msg( 'boop' );
* // returns null
*/
function id2msg( id ) {
	if ( !isString( id ) ) {
		throw new TypeError( format( '0Nt3B,IL', id ) );
	}
	if ( !db ) {
		db = database();
	}
	if ( hasOwnProp( db, id ) ) {
		return db[ id ];
	}
	return null;
}


// EXPORTS //

export default id2msg;
